import { Injectable } from '@angular/core';
import { DefinitionType } from '@helix/platform/shared/api';
import { IRowDataItem } from '@helix/platform/view/api';
import { CopyDefinitionComponent } from './copy-definition.component';
import { RxModalService } from '@helix/platform/ui-kit';

@Injectable({
  providedIn: 'root'
})
export class RxCopyDefinitionService {
  constructor(private rxModalService: RxModalService) {}

  openCopyDefinitionComponentModal(
    row: IRowDataItem,
    definitionType: DefinitionType,
    editFragment?: string
  ): Promise<any> {
    return this.rxModalService.openModal({
      content: CopyDefinitionComponent,
      data: {
        definitionName: row.name,
        definitionType,
        editFragment
      },
      size: 'sm'
    });
  }
}
